.SceneHolderM{
    background-image:linear-gradient(141deg, #9fb8ad 0%, #1fc8db 51%, #2cb5e8 75%);
    width:100%;
    height:60vh;
    position: relative;
    overflow-x:hidden;
    /* background-color:whitesmoke; */
}

#bigMountainM{
    position: absolute;
    z-index:1;
    height:25rem;
    bottom:0;
    left:5%;
    animation-name:pop-1;
    animation-duration: 1s;
    animation-fill-mode: forwards;
    visibility: hidden;
}

#mediumMountainM{
    position: absolute;
    z-index: 1;
    height: 20rem;
    bottom:0;
    right:15%;
    animation-name:pop-1;
    animation-duration: 1s;
    animation-fill-mode: forwards;
    visibility: hidden;
}

#tallTree1M{
    position: absolute;
    z-index:2;
    height:12rem;
    bottom:0;
    left:0;
    right:0;
    animation-name:pop-2;
    animation-duration: 2s;
    animation-fill-mode: forwards;
    visibility: hidden;
}

#mediumTree1M{
    position: absolute;
    z-index:2;
    height: 7rem;
    bottom:0;
    right:20%;    
    animation-name:pop-2;
    animation-duration: 2s;
    animation-fill-mode: forwards;
    visibility: hidden;
}

#tallTree2M{  
    position: absolute;
    z-index:3;
    height: 12rem;
    bottom:0;
    right:0;  
    animation-name:pop-2;
    animation-duration: 2s;
    animation-fill-mode: forwards;
    visibility: hidden;
}

#mediumTree2M{
    position: absolute;
    z-index:2;
    height: 8rem;
    bottom:0;
    left:20%;
    animation-name:pop-2;
    animation-duration: 2s;
    animation-fill-mode: forwards;
    visibility: hidden;
}

@keyframes pop-1{
    from{
        transform: translateY(10rem);
        visibility: visible;
    }
    to{
        transform: translateY(0rem);
        visibility: visible;
    }
}

@keyframes pop-2{
    from{
        transform: translateY(10rem);
        visibility: visible;
    }
    to{
        transform: translateY(0rem);
        visibility: visible;
    }
}